@font-face {
  font-family: "Roboto Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 150%;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  font-size: inherit;
  line-height: inherit;
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

.posts {
  width: 100%;
  max-width: 1280px;
  font-family: "Roboto";
  background: #00000087;
  padding: 50px 0;
  margin: 0 auto;
}
@media (max-width: 767.99px) {
  .posts {
    padding: 25px 0;
  }
}

.posts__title {
  font-family: "Roboto Bold";
  font-size: 30px;
  line-height: 150%;
  text-align: center;
  color: deepskyblue;
  margin: 0 0 50px;
}
@media (max-width: 767.99px) {
  .posts__title {
    margin: 0 0 25px;
  }
}

.posts__search {
  width: 50%;
  font-family: "Roboto Bold";
  font-size: 20px;
  line-height: 150%;
  display: block;
  border: none;
  border-radius: 10px;
  outline: 1px solid black;
  padding: 5px 10px;
  margin: 0 auto 50px;
}
.posts__search:focus {
  outline: 1px solid deepskyblue;
}
@media (max-width: 767.99px) {
  .posts__search {
    width: calc(100% - 50px);
    margin: 0 auto 25px;
  }
}

.posts__list {
  height: auto;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  padding: 0 50px;
  margin: 0;
}
@media (max-width: 767.99px) {
  .posts__list {
    grid-template-columns: 1fr;
    padding: 0 25px 25px 25px;
  }
}

.posts__item {
  position: relative;
  background: white;
  border: 1px solid deepskyblue;
  border-radius: 0 0 10% 10%;
  padding: 0 10px 10px;
}

.posts__item-id {
  width: 30px;
  height: 30px;
  font-family: "Roboto Bold";
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid deepskyblue;
  border-radius: 0 0 50% 50%;
  padding: 5px;
  margin: 0 auto;
}

.posts__item-title {
  font-family: "Roboto Bold";
  text-align: center;
  font-size: 18px;
  color: deepskyblue;
}

.posts__item-description {
  text-align: center;
}

.posts__item-button-delete {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  right: 0;
  background: url("../img/close.svg") no-repeat 0 0/15px 15px;
  border: none;
  padding: 0;
  cursor: pointer;
}

.posts__button-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 50px 50px 0;
}
@media (max-width: 767.99px) {
  .posts__button-container {
    padding: 0 25px;
  }
}

.posts__button-prev,
.posts__button-next {
  user-select: none;
  color: white;
  background: deepskyblue;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.posts__button-prev.disabled,
.posts__button-next.disabled {
  opacity: 50%;
  pointer-events: none;
  cursor: not-allowed;
}

.add-post {
  width: 100%;
  max-width: 1280px;
  background: #00000087;
  padding: 0 50px 50px;
  margin: 0 auto;
}
@media (max-width: 767.99px) {
  .add-post {
    padding: 0 25px 25px;
  }
}

.add-post__title {
  font-family: "Roboto Bold";
  font-size: 30px;
  line-height: 150%;
  text-align: center;
  color: deepskyblue;
  margin: 0 0 50px;
}
@media (max-width: 767.99px) {
  .add-post__title {
    margin: 0 0 25px;
  }
}

.add-post__form {
  width: 75%;
  max-width: 700px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: white;
  border-radius: 10px;
  padding: 20px;
  margin: 0 auto;
}
@media (max-width: 767.99px) {
  .add-post__form {
    width: 100%;
  }
}

.add-post__form-title {
  grid-column: 1/-1;
  border: none;
  border-bottom: 1px solid deepskyblue;
  margin: 0 0 20px;
}

.add-post__form-title label {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (max-width: 767.99px) {
  .add-post__form-title label {
    flex-flow: column;
    gap: 10px 0;
  }
}

.add-post__form-title-input {
  border: none;
  border-radius: 10px;
  outline: 1px solid black;
  padding: 0 10px;
}
.add-post__form-title-input:focus {
  border: none;
  outline: 1px solid deepskyblue;
}

.add-post__form-description {
  grid-column: 1/-1;
  border: none;
  border-bottom: 1px solid deepskyblue;
  margin: 0 0 20px;
}

.add-post__form-description label {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (max-width: 767.99px) {
  .add-post__form-description label {
    flex-flow: column;
    gap: 10px 0;
  }
}

.add-post__form-description-textarea {
  width: 60%;
  min-height: 200px;
  height: max-content;
  resize: none;
  border: none;
  border-radius: 10px;
  outline: 1px solid black;
  padding: 0 10px;
}
.add-post__form-description-textarea:focus {
  border: none;
  outline: 1px solid deepskyblue;
}
@media (max-width: 767.99px) {
  .add-post__form-description-textarea {
    width: 100%;
  }
}

.add-post__form-button-reset {
  width: max-content;
  justify-self: center;
  color: white;
  background: red;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
}
@media (max-width: 767.99px) {
  .add-post__form-button-reset {
    padding: 5px;
  }
}

.add-post__form-button-create {
  width: max-content;
  justify-self: center;
  color: white;
  background: deepskyblue;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
}
@media (max-width: 767.99px) {
  .add-post__form-button-create {
    padding: 5px;
  }
}